import React from 'react'
import BottomSection from '../../BottomSection'
import style from "./style.module.css"
import img1 from "../../imgMove/1.webp"
import img2 from "../../imgMove/2.png"
import img3 from "../../imgMove/3.webp"
export default function Products() {
  return (
    <div className={style.main}>
      <div className={style.position}>
        <img src={img1} alt="" />
        <span> </span>
      </div>
      <div className={style.new_p}>
        AutoAgents.aiAIAIAI AgentsAutoAgents.ai/AI
        AutoAgents.aiAutoAgents.ai
      </div>
      <div className={style.imgDiv}>
        <img src={img2} alt="" />
        <span> </span>
        <p> </p>
        <img src={img3} alt="" />
        <span> </span>
        <p></p>
      </div>
      <BottomSection />
    </div>
  )
}
