import React from 'react'
import BottomSection from '../../BottomSection'
import src1 from '../../../imgs/ico/1.png';
import src2 from '../../../imgs/ico/2.png';
import src3 from '../../../imgs/ico/3.png';
import src4 from '../../../imgs/ico/4.png';
import src5 from '../../../imgs/ico/5.png';
import src6 from '../../../imgs/ico/6.png';
import src7 from '../../../imgs/ico/7.png';
import src8 from '../../../imgs/ico/8.png';
import src9 from '../../../imgs/ico/9.png';
import src10 from '../../../imgs/ico/10.png';


import style from "./style.module.css"
export default function AboutUs() {
  const arrSrc = [src1, src2, src3, src4, src5, src6, src7, src8, src9, src10];
  return (
    <div className={style.main}>
      <div className={style.imgDiv}>
        <span className={style.animatedElement}>Meet our Team</span>
        <p>
          1010 AI/
        </p>
        {
          arrSrc.map((item, index) => (
            <img key={index} src={item} alt="" />
          ))
        }
      </div>
      <BottomSection />
    </div>
  )
}
