import React from 'react'
import BottomSection from "../../BottomSection"
import imgVideo from '../../imgMove/.webp'
import img1 from "../../imgMove/1.webp"
import img2 from "../../imgMove/2.webp"
import img3 from "../../imgMove/3.webp"
import style from "./style.module.css"
export default function HomePage() {
  return (
    <div className={style.main}>
      <div className={style.father}>
        <img src={imgVideo} alt="" />
        <span>:</span>
        <span></span>
      </div>
      <div className={style.language}>
        <span></span>
      </div>
      <div>
        <img src={img1} alt="" />
      </div>
      <div className={style.language}>
        <span></span>
      </div>
      <div>
        <img src={img2} alt="" />
      </div>
      <div className={style.language}>
        <span>10</span>
      </div>
      <div>
        <img src={img3} alt="" />
      </div>
      <div className={style.languageP}>
        <p>&nbsp;</p>
        <p></p>
        <p>&nbsp;</p>
        <p></p>
        <p>&nbsp;</p>
        <p>AI</p>
      </div>
      <BottomSection />
    </div>
  )
}
